	&__logo {
		display: block;
		margin-bottom: 100px;
		padding: 0 35px;
		@include r(767) {
			margin-bottom: 80px;
			padding: 0 27px; }
		&-pic {
			display: block;
			position: relative;
			overflow: hidden;
			&:before,
			&:after {
				content: "";
