	&__btn {
		position: absolute;
		top: 247px;
		left: 100%;
		width: 56px;
		padding: 0 10px;
		height: 62px;
		background: #000;
		font-size: 0;
		@include r(767) {
			top: 140px;
			width: 46px;
			height: 50px; }
		&:before,
		&:after {
			content: "";
			position: absolute;
			width: 20px;
			left: 50%;
			margin-left: -10px;
			transition: all .4s; }
		&:before {
			top: 25px;
			height: 2px;
			background: #FFF;
			@include r(767) {
				top: 19px; } }
		&:after {
			top: 30px;
			height: 7px;
			box-shadow: 0 2px 0 0 #fff inset, 0 -2px 0 0 #fff inset;
			@include r(767) {
				top: 24px; } }
		&_white {
			background: #FFF;
