		padding-top: 17px;
		@include r(767) {
			margin-bottom: 15px; }
		&:before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 12px;
			height: 1px;
			background: #B5D6EA; } }
	&__phone,
